<script>
  import { fade } from "svelte/transition";
  import { onDestroy } from "svelte";
  import { router } from "~router";
  import { routes } from "./router_config.js";

  const { component, destroy, route, current } = router(routes);

  let steps;
  let step = 0;

  onDestroy(destroy);

  const prev = () => {
    if (!steps || !steps[step]) {
      route(routes[current() + 1][0]);
      step = 0;
    } else {
      steps[step++]();
    }
  };

  const next = () => {
    if (!steps || !steps[step]) {
      route(routes[current() - 1][0]);
      step = 0;
    } else {
      steps[step--]();
    }
  };

  const on_right = (node, cb) => {
    const handle_down = ({ which }) => {
      if (which === 39) cb();
    };

    node.addEventListener("keydown", handle_down);

    return {
      destroy: () => {
        node.removeEventListener("keydown", handle_down);
      },
    };
  };

  const on_left = (node, cb) => {
    const handle_down = ({ which }) => {
      if (which === 37) cb();
    };

    node.addEventListener("keydown", handle_down);

    return {
      destroy: () => {
        node.removeEventListener("keydown", handle_down);
      },
    };
  };
</script>

<svelte:window
    use:on_left={next}
    use:on_right={prev}
/>

<svelte:component this={$component} bind:steps />


<style>
    :global(body) {
        display: flex;
    }
    p {
        margin: auto;
        font-family: sans-serif;
    }
</style>

{#if steps}
    <p>Press the right arrow once to start the show.</p>
{/if}
